import { Section } from 'react-scroll-section';
import Heading2 from '../../../atoms/Heading2/Heading2';

export default function EmployerAboutIVF() {
  return (
    <Section
      id="about"
      className="mx-auto mb-10 flex min-h-[500px] items-center px-6 font-primary md:mt-12 lg:w-3/5"
    >
      <div className="flex-1">
        <Heading2 title="About IPB Job Fair" />
        <p className="text-center">
          As a bridge between the graduates and industrial world,
          <strong> CDA IPB </strong>
          always try its best to prepare IPBstudents and graduates to adapt, be flexible, and be agile towards those changes. In accordance with this events slogan “Be Ready for your Next Level”,
          <strong> CDA IPB </strong>
          feels that its necessary to develop the values, skills, and characters of its students and graduates in order to be able to narrow down on the needs of the industry today. Various activities to manifest that mission we hold regularly, one of them is
          <strong> IPB JOBFAIR</strong>
        </p>
      </div>
    </Section>
  );
}
