import { CSSProperties } from 'react';
import { Carousel } from 'react-responsive-carousel';
import { isMobile } from 'react-device-detect';
import { Section } from 'react-scroll-section';

interface EmployerCarouselProps {
  className?: string;
  wrapperStyle?: CSSProperties;
  bannerClassName?: string;
}

export default function EmployerCarousel(props: EmployerCarouselProps) {
  const { className, wrapperStyle, bannerClassName } = props;
  return (
    <Section
      id="home"
      className="flex items-center blue-gradient2 shadow-lg"
      style={wrapperStyle || { minHeight: 'calc(100vh - 80px)' }}
    >
      <Carousel
        showArrows
        showThumbs={false}
        autoPlay
        interval={5000}
        infiniteLoop
        stopOnHover
        swipeable
        emulateTouch
        thumbWidth={100}
        showStatus={false}
        showIndicators={!isMobile}
        className={className || 'mb-5 p-5 shadow lg:min-h-[500px]'}
      >
        <div>
          <img
            src="/img/banners/banner-1.jpg"
            alt="banner-1"
            className={bannerClassName || 'rounded-lg'}
          />
        </div>
        <div>
          <img
            src="/img/banners/banner-2.jpg"
            alt="banner-2"
            className={bannerClassName || 'rounded-lg'}
          />
        </div>
      </Carousel>
    </Section>
  );
}
